.container{
    width: 1100px;
    height: 300px;
    position: relative;
    margin: 20px auto;
}
.reset{
    text-decoration: none;
    color: cornflowerblue;
}
.reset:hover{
    text-decoration: underline;
    cursor: pointer;
}
.title{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    text-align: center;
    padding-top: 20px;
}
.container2{
    width: 80%;
    height: 300px;
    float: right;
}
.container3{
    width: 20%;
    height: 300px;
    position: relative;
}
.sizing{
    width: 100%;
    height: inherit;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
}
.text{
    padding: 30px 50px;
}
.ulList{
    background-color: rgba(0, 0, 0,0.5);
    color: #ddd;
    list-style: none;
    width: 220px;
    height: 300px;
    position: absolute;
    top: 0; left: 0;
    margin: 0;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    opacity: 0;
    padding-left: 0;
}
.ulList:hover{
    opacity: 1;
}
.List{
    position: relative;
    top: 100px;
    text-align: center;
}